@font-face {
  font-family: "PT Sans Narrow";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/ptsansnarrow.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "PT Sans Narrow";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/ptsansnarrowbold.woff2") format("woff2");
  font-display: swap;
}
html {
  height: 100%;
  font-size: 18px;
  font-family: "PT Sans Narrow", "Arial", sans-serif;
  line-height: 27px;
}
img {
  max-width: 100%;
  height: auto;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.page_header {
  display: flex;
  flex-direction: column;
  color: white;
  background-color: #242424;
}
.container {
  min-height: 100%;
  min-width: 320px;
  margin: 0;
  background-color: #f8f5f2;
  color: #000000;
}
@media (min-width: 768px) {
  .site-list {
    width: 570px;
  }
}
@media (min-width: 1068px) {
  .site-list {
    width: 780px;
  }
}
@media (min-width: 768px) {
  .user-list {
    width: 60px;
  }
}
.stats {
  height: 543px;
  background-color: #292929;
  color: white;
}
@media (min-width: 768px) {
  .stats {
    height: 497px;
    width: auto;
    margin: 0 auto;
  }
}
.page_header__logo {
  height: 85px;
}
@media (min-width: 768px) {
  .page_header__logo {
    order: 2;
    height: 400px;
  }
}
@media (min-width: 1068px) {
  .page_header__logo {
    height: 648px;
  }
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}
@media (min-width: 768px) {
  .main-nav__wrapper {
    display: flex;
    justify-content: space-between;
    width: 640px;
    margin: 0 auto;
  }
}
@media (min-width: 1068px) {
  .main-nav__wrapper {
    width: 960px;
  }
}
.stats__header {
  padding: 27px 20px 0;
  display: flex;
  flex-direction: column;
  color: black;
  background-color: white;
}
@media (min-width: 768px) {
  .stats__header {
    padding: 25px 34px 30px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    text-transform: none;
  }
}
.stats__list {
  display: flex;
  flex-direction: column;
  background-color: #353535;
}
@media (min-width: 768px) {
  .stats__list {
    height: 318px;
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
}
.stats__wrapper {
  display: flex;
  flex-direction: column;
  width: 320px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .stats__wrapper {
    width: 640px;
  }
}
.stats__intro {
  margin-bottom: 13px;
  text-align: center;
  text-transform: uppercase;
  color: black;
  font-family: "PT Sans Narrow", "Arial", sans-serif;
  font-weight: 700;
  font-size: 30px;
  line-height: 30px;
}
.stats__slogan {
  margin-bottom: 28px;
  text-align: center;
  font-family: "PT Sans Narrow", "Arial", sans-serif;
  font-weight: 400;
  font-size: 20px;
  line-height: 27px;
}
.stats__legend {
  font-family: "PT Sans Narrow", sans-serif;
  font-weight: 400;
  font-size: 20px;
  line-height: 20px;
}
.stats__item {
  height: 80px;
}
.stats__item:first-child {
  background-color: rgba(45, 38, 130, 0.05);
}
.stats__item:nth-child(2n) {
  background-color: rgba(255, 255, 255, 0.12);
}
.stats__legend--dekstop {
  color: rgba(0, 0, 0, 0.3);
  display: none;
}
@media (min-width: 768px) {
  .stats__legend--dekstop {
    display: block;
  }
}
.stats__legend--mobile {
  margin-top: auto;
  color: rgba(255, 255, 255, 0.3);
}
@media (min-width: 768px) {
  .stats__legend--mobile {
    display: none;
  }
}
